@import "./functions";
@import "./colors";
.tns-outer {
    width: 100%;
    margin-top: em(4);
    position: relative;
    left: 0;
    & [aria-controls],
    & [data-action] {
        cursor: pointer;
    }
    .tns-controls {
        button {
            font-size: em(28);
            position: absolute;
            z-index: 1;
            bottom: 0;
            top: 0;
            width: em(40, 28);
            height: em(80, 28);
            margin: auto 0;
            padding: 0;
            color: #5c5260;
            border: none;
            border-radius: em(40, 28);
            background: $background-color;
            opacity: 0.3;
            :hover>& {
                opacity: 0.7;
            }
            &:hover,
            &:active,
            &:focus {
                opacity: 1;
            }
            &[data-controls="prev"] {
                left: 0;
                border-radius: 0 em(55, 28) em(55, 28) 0;
            }
            &[data-controls="next"] {
                right: 0;
                border-radius: em(55, 28) 0 0 em(55, 28)
            }
        }
    }
    .tns-nav {
        display: flex;
        position: absolute;
        bottom: em(15);
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        z-index: 1;
        text-align: center;
        &>[aria-controls] {
            width: em(8);
            height: em(8);
            padding: em(8);
            margin: em(5);
            border-radius: 50%;
            background: #ddd;
            opacity: 0.9;
            border: 0;
            margin-top: em(10);
        }
        &>.tns-nav-active {
            background: rgb(131, 131, 131);
        }
    }
    #hero {
        position: relative;
        margin: 0 auto;
        article {
            &.tns-item {
                position: relative;
                img {
                    min-width: 100%;
                    max-height: em(500);
                    object-fit: cover;
                }
                h2 {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translateY(calc(-50% - #{em(32/2)})) translateX(-50%);
                    color: $text-color;
                    background-color: $background-color;
                    opacity: 0.7;
                    padding: em(32);
                }
            }
        }
        .tns-controls {
            text-align: center;
            margin-bottom: em(10);
        }
    }
}

.tns-visually-hidden {
    position: absolute;
    left: -10000em;
}