@import "./info";

.card {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, .125);
    border-radius: .25rem;
    margin-bottom: 30px;
    header {
        h2 {
            padding: 1.25rem 1.25rem 0;
            display: block;
        }
        img {
            max-width: 100%;
            height: auto;
            overflow: hidden;
        }
    }
    p {
        padding: 0 1.25rem 1.25rem;
        line-height: 1.58;
        letter-spacing: -.004em;
        margin: 0;
    }
    footer {
        margin-top: auto;
        align-self: flex-end;
        width: 100%;
        padding: 0 1.25rem 1rem;
        display: flex;
        border: 0;
        align-items: center;
        &>a {
            align-self: flex-end;
            margin-left: auto;
            padding: 8px;
        }
        .post__info {
            @extend %info
        }
    }
}