@import "./colors";
@import "tiny-slider";
@import "./normalize";
@import "presets";
@import "./postbox";
@import "pagination";
@import "./functions";
@import "./hero";
@import "./info";
$max-width: em(1032);
$follow-med: em(1011);
%contentContainer {
    max-width: em(740);
    padding-left: em(20);
    padding-right: em(20);
    margin-left: auto;
    margin-right: auto;
}

%siteContainer {
    max-width: $max-width !important;
    margin-left: auto;
    margin-right: auto;
}

header[role="banner"] {
    border-bottom: em(1) solid rgba(0, 0, 0, .125);
    &::after {
        content: '';
        display: block;
        width: 100%;
        height: em(12);
        @media (min-width: $follow-med) {
            height: em(34);
        }
        background-color: $accent-color;
    }
    #banner-inner {
        @extend %siteContainer;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &>a {
            padding: em(8) em(12) 0 em(8);
            margin: 0;
            &>svg {
                z-index: 90;
                height: em(70);
                width: auto;
                fill: black;
                position: absolute;
                top: em(4);
                @media (min-width: $follow-med) {
                    height: em(190);
                    top: em(4);
                }
            }
        }
        &>nav {
            z-index: 100;
            top: 0;
            left: 0;
            position: fixed;
            height: 100vh;
            overflow: hidden;
            width: 100%;
            display: block;
            transform: translate(-100%, 0);
            transition: transform 0.3s ease;
            background-color: $background-color;
            padding-top: em(60);
            @media (min-width: $follow-med) {
                padding-top: initial;
                transform: translate(0, 0);
                display: flex !important;
                width: initial;
                height: em(34*2);
                padding-top: em(24);
                background-color: transparent;
                position: relative;
                box-sizing: unset;
                display: block;
                vertical-align: middle;
                margin-left: auto;
            }
            #close {
                cursor: pointer;
                background-color: transparent;
                border: 0;
                padding: 8px 15px;
                position: fixed;
                right: 0;
                top: em(6);
                @media (min-width: $follow-med) {
                    display: none;
                }
            }
            &.open {
                transform: translate(0, 0);
            }
            ul {
                list-style-type: none;
                margin: 0;
                padding: 0;
                @media (min-width: $follow-med) {
                    display: flex;
                }
                li {
                    display: block;
                    a {
                        display: block;
                        vertical-align: middle;
                        padding: em(10, 24) em(12, 24) em(12, 24) em(32, 24);
                        width: 100%;
                        text-decoration: none;
                        font-size: em(24);
                        font-weight: 700;
                        &:hover,
                        &:focus {
                            background-color: $primary-color;
                            color: $background-color;
                        }
                        @media (min-width: $follow-med) {
                            width: auto;
                            color: $primary-color;
                            font-size: em(16);
                            padding: em(10) em(12) em(12) em(12);
                        }
                    }
                }
            }
            .active {
                background-color: #4CAF50;
                color: $background-color;
            }
        }
        &>#menu {
            box-sizing: unset;
            display: block;
            margin: 0 em(4);
            padding: em(12);
            overflow: hidden;
            vertical-align: middle;
            cursor: pointer;
            height: em(24);
            width: em(24);
            margin-left: auto;
            @media (min-width: $follow-med) {
                display: none;
            }
        }
    }
}

#follow {
    //float: left;
    display: flex;
    position: sticky;
    z-index: 10;
    top: 0;
    width: 100%;
    $follow-wid: $max-width;
    background: $background-color;
    border-bottom: 1px solid rgba(0, 0, 0, 0.10);
    @media (min-width: $follow-med) {
        background: transparent;
        float: left;
        width: em(48);
        flex-direction: column;
        position: fixed;
        top: 50%;
        transform: translateY(-50%);
        left: calc( 50% - #{$max-width/2});
        border-bottom: none;
    }
    %icon {
        flex-grow: 1;
        display: block;
        text-align: center;
        padding: em(12);
        transition: all 0.3s ease;
        color: $text-color;
        font-size: em(18);
        @media (min-width: $follow-med) {
            display: inline;
            width: em(44);
        }
        &:hover,
        &:focus {
            background-color: $accent-color;
            color: $background-color;
        }
    }
    .facebook {
        @extend %icon;
    }
    .twitter {
        @extend %icon;
    }
    .instagram {
        @extend %icon;
    }
    .linkedin {
        @extend %icon;
    }
    .youtube {
        @extend %icon;
    }
    .spotify {
        @extend %icon;
    }
    .itunes {
        @extend %icon;
    }
    .plus {
        @extend %icon;
    }
}

.post {
    &__header {
        @extend %contentContainer;
        max-width: em(830);
        margin-bottom: em(12);
        h1 {
            display: block;
            padding-top: 12px;
            margin: 0;
        }
        .post__info {
            @extend %info
        }
    }
    &__comments  {
        @extend %contentContainer;
        max-width: em(830);

    }

    figure {
        padding: 0;
        margin: 0;
        &.full img {
            width: 100%;
            max-height: 40em;
            height: auto;
            margin: 0;
            padding: 0;
            object-fit: cover;
            object-position: 50% 10%
        }
    }

    &__content {
        &>h2, &>h3, &>h4  {
            @extend %contentContainer;
            max-width: em(570); 
        }
        &>p, &>ol{
            @extend %contentContainer;
            
        }
        &>figure {
            @extend %contentContainer;
            max-width: em(740);
            padding-left: em(20);
            padding-right: em(20);
            margin-left: auto;
            margin-right: auto;
            width: 100%;
            &.full {
                padding: 0;
                margin: 0;
                &.full img {
                    width: 100%;
                    max-height: 40em;
                    height: auto;
                    margin: 0;
                    padding: 0;
                    object-fit: cover;
                    object-position: 50% 10%
                }
            }
        }
    }
    &__tags {
        @extend %contentContainer;
        max-width: em(830);
        ul {
            list-style: none;
            margin: 1em 0 3rem 0;
            padding: 0;
            li {
                display: inline-block;
                font-size: 0.9rem;
                line-height: em(34);
                a {
                    background: rgba(0, 0, 0, 0.05);
                    color: rgba(0, 0, 0, 0.6);
                    border-radius: 3px;
                    padding: em(5) em(10);
                    &:hover {
                        background: rgba(0, 0, 0, 0.07);
                        text-decoration: none;
                    }
                }
            }
        }
    }
    &__more {
        @extend %siteContainer;
        display: flex;
        flex: 1 0 auto;
        padding: em(20);
        a {
            display: block;
            cursor: pointer;
            height: 24px;
            line-height: 24px;
            overflow: hidden;
            vertical-align: middle;
            &:first-child {
                align-self: flex-start;
            }
            &:last-child {
                display: inline-block;
                margin: 0 4px;
                align-self: flex-end;
                margin-left: auto;
            }
        }
    }
}

