.jump {
    border-top: 1px solid rgba(0, 0, 0, 0.10) !important;
    padding: 20px;
    background: rgba(0, 0, 0, 0.05);
    header {
        @extend %siteContainer;
    }
    &__tags {
        @extend %siteContainer; //@extend %contentContainer;
        display: flex;
        flex-wrap: wrap;
        a {
            text-transform: lowercase;
            background: $primary-color;
            padding: 8px;
            border-radius: 30px;
            color: black;
            font-weight: 500;
            font-size: 0.8rem;
            margin: 4px 4px 4px 0;
        }
    }
}

.footer {
    border-top: 1px solid rgba(0, 0, 0, 0.10) !important;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 1px;
    padding-bottom: 12px;
    color: rgba(0, 0, 0, 0.44);
    position: relative;
    p {
        font-size: 0.7em;
    }
}

.list {
    @extend %contentContainer;
    max-width: em(960);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .card {
        flex: 1 0 100%;
        box-sizing: border-box;
        padding: em(10);
        margin: em(4);
    }
    @media (min-width: em(570)) {
        .card {
            flex: 1 0 calc(50% - #{em(8)});
        }
    }
    @media (min-width: em(810)) {
        .card {
            flex: 1 0 calc(33% - #{em(16)});
        }
    }
}

.tns-outer {
    @extend %contentContainer;
    max-width: em(910);
}

.section-title {
    @extend %contentContainer;
    max-width: em(910);
    border-bottom: em(1) solid rgba(0, 0, 0, .125);
    margin: em(22) em(24) em(12);
    padding: 0;
    @media (min-width: em(910)) {
        max-width: em(910);
        margin: em(22) auto em(12);
    }
    h1 {
        font-weight: 700;
        font-size: 1.4rem;
        padding: 0;
        margin: 0;
    }
}